<template>
    <div class="sc_search">
        <span class="icon icon-left" style="width:7%;" v-on:click='back_page()'></span>
        <div class="search-input">
            <label class="icon icon-search" for="search" style="font-size:.7rem;margin-left:.3rem;"></label>
            <input type="search" id='search' placeholder='' class="sc_searchbox_search" v-on:keydown="enter"/>
        </div>
        <span v-on:click="search_word" class="sc_searchbox_button"></span>
    </div>
</template>

<script>

    export default {
        name: 'sc_search',
        data(){
            return{
                foods:[],//
            }
        },
        mounted(){
            //
            let keyword = this.$route.query.keyword;
            if(keyword){
                this.searchWord({
                    keyword:keyword,
                    lat:$.cookie('lat'),
                    lit:$.cookie('lit'),
                })
            }
        },
        methods:{
            back_page(){
                this.$router.push('/');
            },
            enter(e){
                if(e.keyCode==13){
                    this.search_word();
                }
            },
            searchWord(data){
                /*
                 peisong:this.peisongfs[0],
                 xiaofei:this.xiaofei[0],
                 huodong:this.huodong[0],
                 shangjia:shangjia,
                 */
                // 
                if(typeof(Storage)!=="undefined"){
                    let arr = localStorage.getItem('searchHistory');
                    arr = String(arr).split(',');
                    // null
                    if(arr[0]=='null'){
                        arr.shift();
                    }
                    //  
                    if( !(arr.indexOf(data.keyword)+1) ){
                        if(arr.length>=10)
                        {
                            arr.pop();
                        }
                        arr.unshift(data.keyword)
                    }
                    localStorage.setItem('searchHistory',arr);
                }

                $(search).val(data.keyword);//

                // ajax 
                $.get('/search',data,data=>{
                    //keyvalue   
                    let chunk = data['0']||data['1']||data['3'];

                    //chunk   
                    if(chunk){
                        let length = chunk.restaurant_with_foods.length;
                        for(let i = 0;i<length;i++){
                            this.foods.push(chunk.restaurant_with_foods[i]);
                        }
                    }
                    this.$emit('get_food',this.foods);
                });
            },//  
            search_word(){
                let val = $(search).val().trim();
                if(this.$route.query.keyword!=val){
                    if(val)
                    {
                        location.href = 'http://'+location.host+'/#/searchfoods?keyword='+val;
                        location.reload();
                    }
                }
            },
        }
    }
</script>

<style scoped>

    /*  */
    .sc_search{
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        padding:.6rem .3rem;
    }
    .search-input{
        width:76%;
    }
    .sc_searchbox_search{
        background:#f8f8f8 !important;
        border:none !important;
        text-indent:.3rem;
        font-size:.6rem !important;
        border-radius: .05rem !important;
    }
    .sc_searchbox_button{
        width:10%;
        font-size:.8rem;
        font-weight:bold;
    }

</style>



