<template>
    <main class="sf_search_search">
        <search></search>
        <section class="ss_historybox" v-if="show_history">
            <div class="ss_title">
                <h3 style="font-size:.8rem;margin-top:-0.2rem;"></h3>
                <img src="./img/lajitong.png" alt="" style="width:.8rem;height:.8rem;" v-on:click="delete_history"/>
            </div>
            <div class="ss_itemBox">
                <router-link class="ss_item" :key="item.id" v-for="item in historyItems" :to="{path:'/searchfoods',query:{keyword:item}}">{{item}}</router-link>
            </div>
        </section>
        <section class="ss_hotsearchbox">
            <div class="ss_title">
                <h3 style="font-size:.8rem;margin-top:-0.2rem;"></h3>
            </div>
            <div class="ss_itemBox">
                <!--  v-on:click="$emit('search_search',item)" -->
                <router-link class="ss_item" :to="{path:'/searchfoods',query:{keyword:item}}" :key="item.id" v-for="item in hotItems">{{item}}</router-link>
            </div>
        </section>
    </main>
</template>

<script>
    import search from './search.vue'

    //
    function getHotSearch(data){

        //
        let randomArr=()=>{
            //10
            let arr = [];
            for(let Arri = 0;Arri<10;Arri++){
                let num = Math.floor(Math.random()*data.length);

                //  
                !(arr.indexOf(num)+1)?arr.push(num):Arri--;
            }

            //
            let arr1 = [];
            arr.map(item=>{
                arr1.push(data[item].text)
            });

            this.hotItems = arr1;
        };

        if(!(data === 'err'))
        {
            randomArr();
        }

    }

    //
    function get_history(){
        let history = localStorage.getItem('searchHistory');

        // null
        if(!history){
            history = '';
        }

        this.historyItems = history.split(',');
    }

    export default{
        name:'search_search',
        data(){
            return {
                searchText:'',
                historyItems:[],
                hotItems:[],
                show_history:true,
            }
        },
        mounted(){
            //
            get_history.call(this);
            if(!localStorage.getItem('searchHistory')){
                this.show_history = false;
            }

            //
            $.get('/hotSearch',data=>{
                getHotSearch.call(this,data);
            });
        },
        methods:{
            delete_history(){
                localStorage.removeItem('searchHistory');
                this.historyItems = [];
                this.show_history = false;
            }
        },
        components:{
            search,
        }
    }
</script>

<style scoped>
    /*  */
    .sf_search_search{
        z-index:99;
        position: relative;
        width:100%;
        height:100%;
        background: #fff;
    }

    /*  */
    .ss_title{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding:.4rem .6rem;
    }
    .ss_itemBox{
        padding:.3rem .5rem;
    }
    .ss_item{
        display: inline-block;
        margin:.3rem .15rem;
        padding:0 .5rem;
        background: #f7f7f7;
        line-height:1.6rem;
        font-size:.6rem;
        color:#666;
        border-radius: .1rem;
    }

</style>


