<template>
    <div class="hb_hongbao">
        <h_header class="hb_h_header" :title="''"></h_header>
        <main>
            <section class="hb_head">
                <div class="hb_head_hb" v-on:click="is_hongbao=true">
                    <span :class="is_hongbao?'hb_head_text hb_head_active':'hb_head_text'"> {{hongbao.length}} </span>
                </div>
                <div class="hb_head_djj" v-on:click="is_hongbao=false">
                    <span :class="is_hongbao?'hb_head_text':'hb_head_text hb_head_active'"> {{daijinquan.length}} </span>
                </div>
            </section>
            <section v-if="is_hongbao">
                <section class="hb_ad">
                    <a href="https://h5.ele.me/commend/#/" class="hb_ad_box">
                        <div class="hb_ad_text">
                            <h4 class="hb_ad_title"></h4>
                            <p class="hb_ad_tip">5</p>
                        </div>
                        <img class="hb_ad_img" src="http://shadow.elemecdn.com/faas/h5/static/commend.ca533b7.png" alt=""/>
                    </a>
                    <a href="https://h5.ele.me/freelunch/#/" class="hb_ad_box hb_shadow">
                        <div class="hb_ad_text">
                            <h4 class="hb_ad_title"></h4>
                            <p class="hb_ad_tip">20</p>
                        </div>
                        <img class="hb_ad_img" src="data:image/png;base64," alt=""/>
                    </a>
                </section>
                <section class="hb_item hb_shadow" v-for="item in hongbao" :key="item.id">
                    <div :class="item.name==''?'hb_item_info hb_item_svip':'hb_item_info'" >
                        <div class="hb_item_price">
                            <span class="hb_item_price_num" :style="item.name==''?'color:#a17c3b':''">{{item.amount}}</span>
                            <p class="hb_item_price_text">{{item.description_map.sum_condition}}</p>
                        </div>
                        <div class="hb_item_person">
                            <h4 class="hb_item_person_title">{{item.name}}</h4>
                            <div class="hb_item_person_time">{{item.description_map.validity_periods}}</div>
                            <div class="hb_item_person_tip">{{item.description_map.phone}}</div>
                        </div>
                        <div class="hb_item_time">{{item.description_map.validity_delta}}</div>
                    </div>
                    <div :class="item.name==''?'hb_item_tips hb_item_svip':'hb_item_tips'">
                        <p class="hb_item_tips_tip" v-for="info in item.limit_map" :key="info.id">{{info}}</p>
                    </div>
                </section>
                <section class="hb_notMore" v-if="notMore">
                    ~
                </section>
                <section class="hb_other">
                    <router-link class="hb_other_link" to="/wode/hongbao/hbHistory"></router-link>
                    <a href="https://h5.ele.me/service/#hongbao" class="hb_other_link"></a>
                </section>
            </section>
            <section class="hb_djqBox" v-if="!is_hongbao">
                <section class="hb_djqItem hb_shadow" v-for="item in daijinquan">
                    <div class="hb_djqItem_price">
                        <span class="hb_djqItem_price_num">{{item.reduce_amount}}</span>
                    </div>
                    <div class="hb_djqItem_person">
                        <h4 class="hb_djqItem_person_title">{{item.name}}</h4>
                        <div class="hb_djqItem_person_tips" v-for="info in item.details">{{info}}</div>
                    </div>
                    <router-link :to="{path:'/shop',query:{id:item.restaurant_id}}" class="hb_djqItem_btn"></router-link>
                </section>
                <section class="hb_djqItem_other">
                    <a class="hb_djqItem_more" href="https://h5.ele.me/coupon/#entry_id=211"></a>
                </section>
                <section class="hb_djqItem_other">
                    <a class="hb_djqItem_shuoming" href="https://h5.ele.me/service/#coupon"></a>
                </section>
            </section>
        </main>
    </div>
</template>

<script>
    import h_header from './header.vue';

    export default {
        name: 'hb_hongbao',
        data(){
            return {
                is_hongbao:true,
                hongbao:[],
                notMore:false,
                user_id:'',
                sid:'',
                offset:0,
                daijinquan:[],
            }
        },
        methods:{
            get_hongbao(){
                $.get('/user_info/hongbao',{
                    user_id:this.user_id,
                    sid:this.sid,
                    offset:this.offset
                },data=>{
                    if(data.length){
                        if(data.length<20){
                            this.notMore = true;
                        }
                        data.map(item=>{
                            this.hongbao.push(item);
                        });
                        this.offset+=20;
                    }else if(data.message){
                        this.$router.push('/wode');
                    }
                });
            },//
            get_coupons(){
                $.get('/user_info/coupons',{
                    user_id:this.user_id,
                    sid:this.sid,
                },data=>{
                    this.daijinquan = data;
                });
            },//
        },
        mounted(){
            let userInfo = localStorage.getItem('user_id');
            if(userInfo){
                this.user_id = userInfo.split(';')[0].split('=')[1];
                this.sid = userInfo.split('SID=')[1].split(';')[0];
                this.get_hongbao();
                this.get_coupons();
            }

            $(()=>{
                let win = $(window);
                let dom = $(document);
                win.scroll(()=>{
                    if(!this.notMore){
                        if(win.scrollTop()>(dom.height()-win.height())-2){
                            this.get_hongbao();
                        }
                    }
                });
            });//
        },
        components:{
            h_header
        }
    }
</script>

<style scoped>
    /*  */
    .hb_djqItem_btn{
        width:2.7rem;
        height:1rem;
        margin-right:1rem;
        border: 1px solid #ff0025;
        text-align: center;
        font-size:.5rem;
        color:#ff0025;
        border-radius: 10px;
        white-space: nowrap;
        transform: scale(.9);
    }
    .hb_djqItem_person_tips{
        line-height: .7rem;
        font-size:.5rem;
        color:#999;
    }
    .hb_djqItem_person_title{
        overflow: hidden;
        width:8rem;
        margin-bottom:.1rem;
        font-size:.8rem;
        color:#333;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
    .hb_djqItem_price_num::before{
        position: relative;
        top:-.05rem;
        left:-.1rem;
        content: "\A5";
        vertical-align: baseline;
        font-size: .75rem;
    }
    .hb_djqItem_price_num{
        font-size:1.5rem;
        font-weight: bold;
        color:#ef4661;
    }
    .hb_djqItem_price{
        display: flex;
        justify-content: center;
        width:4rem;
    }
    .hb_djqItem_person{
        flex: 1;
    }
    .hb_djqItem_more{
        display: inline-block;
        margin-top:.15rem;
        padding:.35rem 1.5rem;
        background-color: #fff;
        font-size: .6rem;
        color:#333;
        border-radius: 3px;
    }
    .hb_djqItem_shuoming{
        display: inline-block;
        margin-top: .9rem;
        font-size: .5rem;
        color: #999;
    }
    .hb_djqItem_other{
        text-align: center;
    }
    .hb_djqBox{
        margin-top:4.7rem;
    }
    .hb_djqItem{
        display: flex;
        align-items: center;
        margin:.55rem;
        padding:.8rem 0;
        background-color: #fff;
        border-radius: 4px;
    }

    /*  */
    .hb_item_svip{
        color: #a17c3b;
        background: #fff4be url(data:image/svg+xml;base64,) 0 0 no-repeat  !important;
    }
    .hb_item_tips::before{
        content: '';
        position: absolute;
        top:-.3rem;
        left:-.4rem;
        width:.6rem;
        height:.6rem;
        background-color: #f5f5f5;
        border-radius: .2rem;
    }
    .hb_item_tips::after{
        content: '';
        position: absolute;
        top:-.3rem;
        right:-.4rem;
        width:.6rem;
        height:.6rem;
        background-color: #f5f5f5;
        border-radius: .2rem;
    }
    .hb_item_tips_tip{
        transform: scale(.9);
        transform-origin: 0 0;
    }
    .hb_item_tips{
        position: relative;
        padding:.4rem .7rem .5rem;
        background: #fcfcfc;
        border-top: 1px dashed #ddd;
        font-size:.5rem;
        border-bottom-left-radius: 4px;
        border-bottom-right-radius: 4px;
    }
    .hb_item_time{
        font-size:.5rem;
        color:#ff0025;
    }
    .hb_item_person_tip{
        margin-top:-.2rem;
        font-size:.5rem;
        transform: scale(.9);
        transform-origin: 0 0;
    }
    .hb_item_person_time{
        font-size:.5rem;
        transform: scale(.9);
        transform-origin: 0 0;
    }
    .hb_item_person_title{
        overflow: hidden;
        width:8rem;
        font-size:.8rem;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
    .hb_item_person{
        flex: 1;
        margin-left: .6rem;
    }
    .hb_item_price{
        min-width:4rem;
        text-align: center;
    }
    .hb_item_price_text{
        margin-top:-.1rem;
        font-size: .5rem;
    }
    .hb_item_price_num::before{
        position: relative;
        top:.15rem;
        content: "\A5";
        vertical-align: super;
        font-size: .75rem;
    }
    .hb_item_price_num{
        line-height: 1rem;
        font-size:1.5rem;
        font-weight: bold;
        color:#ff0025;
    }
    .hb_item_info{
        position: relative;
        display: flex;
        align-items: center;
        background-color: #fff;
        padding:.9rem .7rem .7rem .5rem;
        border-top-left-radius: 4px;
        border-top-right-radius: 4px;
    }
    .hb_item{
        margin:.55rem;
        color:#666;
    }

    /*  */
    .hb_other_link:first-child::after{
        content: '|';
        margin:0 .3rem;
        color:#999;
    }
    .hb_other_link{
        color:#999;
    }
    .hb_other{
        display: flex;
        justify-content: center;
        padding-top:.8rem;
        font-size: .4rem;
    }
    .hb_notMore{
        margin:1rem 0;
        text-align: center;
        font-size: .65rem;
    }

    /*  */
    .hb_ad_tip{
        font-size: .5rem;
        color:#999;
    }
    .hb_ad_title{
        font-size: .75rem;
        color:#666;
    }
    .hb_ad_img{
        width:3.8rem;
        height:2.5rem;
    }
    .hb_ad_box{
        display: flex;
        align-items: center;
        width: 49%;
        padding-left:.8rem;
        background-color: #fff;
        border-radius: 2px;
    }
    .hb_ad{
        margin-top:4.2rem;
        display: flex;
        justify-content: space-between;
        height:4.25rem;
        padding:.55rem .55rem 0;
    }


    /*  */
    .hb_hongbao{
        z-index:99;
        position: relative;
        background-color: #f5f5f5;
        height: 100%;
    }
    .hb_shadow{
        box-shadow: 0 .08rem .08rem rgba(0,0,0,.1);
    }


    /*  */
    .hb_h_header{
        position: fixed!important;
        top:0;
        left:0;
        width: 100%;
    }
    .hb_head_text{
        opacity: 0.69;
        padding-bottom: .2rem;
    }
    .hb_head_active{
        opacity: 1 !important;
        border-bottom: 2px solid #fff;
        border-radius: 1px;
    }
    .hb_head{
        position: fixed;
        top:2.2rem;
        left:0;
        display: flex;
        align-items: center;
        width: 100%;
        height: 2rem;
        background-image: linear-gradient(90deg, #00aaff, #0085ff);
        text-align: center;
        font-size: .7rem;
        color:#fff;
    }
    .hb_head_hb{
        flex: 1;
    }
    .hb_head_djj{
        flex: 1;
    }
</style>



