<template>
    <div class="jn_jinbi" v-if="jinbi_shop.length">
        <header class="jn_head">
            <p class="jn_head_Num">{{info.point}}<span class="icon icon-right jn_head_right"></span></p>
            <p class="jn_head_guide"> <a class="jn_head_icon" href="https://h5.ele.me/service/#ecoin"><svg viewBox="0 0 1024 1024" xmlns="http://www.w3.org/2000/svg" p-id="1514" xmlns:xlink="http://www.w3.org/1999/xlink"><path d="M481.5 65.5c-229.2 0-415 185.8-415 415s185.8 415 415 415 415-185.8 415-415-185.8-415-415-415z m0 766.2c-193.6 0-351.2-157.5-351.2-351.2s157.5-351.2 351.2-351.2 351.2 157.5 351.2 351.2-157.6 351.2-351.2 351.2z" fill="#ffffff"></path><path d="M481.5 257.1c-77.2 0-141.6 54.8-156.4 127.7h66.5c13.2-37.1 48.3-63.8 89.9-63.8 52.8 0 95.8 43 95.8 95.8 0 41.6-26.8 76.7-63.8 89.9v5.9h-63.8v127.7h63.8v-67.1c72.9-14.8 127.7-79.2 127.7-156.4-0.1-88.3-71.6-159.7-159.7-159.7zM449.5 704h63.8v63.8h-63.8z" fill="#ffffff"></path></svg></a></p>
        </header>
        <main>
            <section class="jn_main_head">
                <h4 class="jn_main_head_title"></h4>
                <small class="jn_main_head_small"></small>
            </section>
            <section class="jn_main_list">
                <a :href="item.url" class="jn_main_item" v-for="item in jinbi_shop" :key="item.id">
                    <p class="jn_main_item_name">{{item.title}}</p>
                    <div class="jn_main_item_price">
                        <span class="jn_main_price_num">{{item.points_required}}</span>
                        <span class="jn_main_price_text"></span>
                        <span class="jn_main_price_hd">{{item.corner_marker}}</span>
                    </div>
                    <img  class="jn_main_item_img" :src="find_img(item.image_hash)+'?imageMogr/format/webp/thumbnail/!315x195r/gravity/Center/crop/315x195/'" alt=""/>
                </a>
            </section>
            <a class="jn_main_more" href="https://home.m.duiba.com.cn/chome/index?from=login&spm=14695.1.1.1"> <span class="jn_main_more_icon icon icon-right"></span></a>
        </main>
    </div>
</template>

<script>
    export default {
        name: 'jn_jinbi',
        data(){
            return {
                jinbi_shop:[],
                info:''
            }
        },
        methods:{
            find_img(String_path){
                let path = String_path+'';
                let format = path.match(/(jpeg|png|jpg|bmp)/)[0];
                return `https://fuss10.elemecdn.com/${path.slice(0,1)}/${path.slice(1,3)}/${path.slice(3)}.${format}`;
            },//imgimg  967284af9fb1b499192d2476648c3ccfjpeg   https://fuss10.elemecdn.com/9/67/284af9fb1b499192d2476648c3ccfjpeg.jpeg
        },
        mounted(){
            let user_id = localStorage.getItem('user_id');
            let sid = user_id.split('SID=')[1].split(';')[0];
            let id = user_id.split(';')[0].split('=')[1];

            $.get('/user_info',{
                id:id,
                sid:sid
            },data=>{
                this.info = data;
            });
            $.get('/user_info/suggest',data=>{
                this.jinbi_shop = data;
            });
        },
    }
</script>

<style scoped>
    .jn_jinbi{
        z-index:99;
        position: relative;
        height:100%;
        overflow-y: scroll;
        overflow-x: hidden;
    }

    /*  */
    .jn_main_more_icon{
        font-size:.5rem;
        transform: scale(.7);
    }
    .jn_main_more{
        display: flex;
        justify-content: center;
        padding:.5rem 0;
        background-color: #fff;
        border-top: 1px solid #eee;
        text-align: center;
        font-size: .6rem;
        color: #999;
    }

    /*  */
    .jn_main_price_hd{
        padding: 0 1.066667vw;
        height: 3.466667vw;
        border-radius: .533333vw;
        background: #f94843;
        color: #fff;
        font-size: .5rem;
    }
    .jn_main_price_text{
        font-size: .5rem;
        color: #999;
    }
    .jn_main_price_num{
        font-weight: 600;
        font-size: .7rem;
        color: #ff5339;
    }
    .jn_main_item_name{
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        font-size: .7rem;
        color:#333;
    }
    .jn_main_item_img{
        width: 42vw;
        height: 26vw;
    }
    .jn_main_list{
        display: flex;
        flex-wrap: wrap;
    }
    .jn_main_item:nth-child(2n){
        border-left: 1px solid #eee;
    }
    .jn_main_item{
        width: 50%;
        height: 45.333333vw;
        padding: 2.666667vw 4vw;
        border-top: 1px solid #eee;
        box-sizing: border-box;
    }
    .jn_main_list{
        background-color: #fff;
    }

    /*  */
    .jn_main_head_title{
        font-size:.75rem;
    }
    .jn_main_head_small{
        font-size: .5rem;
        color:#999;
        transform: scale(.9);
        transform-origin:50% 0;
        line-height: 3.2vw;
    }
    .jn_main_head{
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        height:16vw;
        background: #fff url(data:image/png;base64,) 50% 50% no-repeat;
        background-size: 50.666667vw 13.333333vw;
    }

    /*  */
    .jn_head_icon{
        width: 13px;
        height:13px;
        margin-left: .2rem;
    }
    .jn_head_guide{
        display: flex;
        align-items: center;
        margin-top: -.4rem;
        font-size:.6rem;
    }
    .jn_head_right{
        font-weight: normal;
        margin-top: -.3rem;
        margin-left: .3rem;
        font-size: .7rem;
    }
    .jn_head_Num{
        font-size: 2.1rem;
        font-weight: bold;
    }
    .jn_head{
        padding:.9rem 1.5rem 0;
        height:29.333333vw;
        background-image: linear-gradient(90deg,#0af,#0085ff);
        color:#fff;
    }
</style>



