<template>
    <div class="dz_dingdan_zuizong" v-if="timeline.length">
        <h_header class="dz_h_header" :title="''"></h_header>
        <ul class="dz_main">
            <li class="dz_main_item" :key="item.id" v-for="item in timeline">
                <div class="dz_main_item_text">
                    <img v-if="!item.in_processing" class="dz_main_item_img" src="data:image/png;base64," alt=""/>
                    <p :style="item.in_processing?'':'color:#3190e8'">{{item.title}}</p>
                    <span class="dz_main_item_time">
                        {{item.formatted_time}}
                    </span>
                </div>
                <p class="dz_main_item_p">{{item.description}}</p>
            </li>
        </ul>
    </div>
</template>

<script>
    import h_header from './header.vue'
    export default {
        name: 'dz_dingdan_zuizong',
        data(){
            return{
                user_id:'',
                sid:'',
                timeline:[],
            }
        },
        mounted(){
            let userInfo = localStorage.getItem('user_id');
            if(userInfo){
                this.user_id = userInfo.split(';')[0].split('=')[1];
                this.sid = userInfo.split('SID=')[1].split(';')[0];

                $.get('/order/info/zuizong',{
                    id:this.$route.query.id,
                    user_id:this.user_id,
                    sid:this.sid,
                },data=>{
                    this.timeline = data;
                });
            }
        },
        components:{
            h_header
        }
    }
</script>

<style>
    .dz_main_item_img{
        position: absolute;
        top:.4rem;
        left:-1.15rem;
        width: 4vw;
        height: 4vw;
    }
    .dz_main_item_p{
        margin-top: -.2rem;
        font-size: .5rem;
        color: #999;
    }
    .dz_main_item_time{
        font-size: .5rem;
        color: #999;
    }
    .dz_main_item_text{
        display: flex;
        justify-content: space-between;
        flex: 1;
    }
    .dz_main_item::before{
        position: absolute;
        top:.5rem;
        left:-.85rem;
        content: '';
        width:5px;
        height:5px;
        background-color: #e6e6e6;
        border-radius: 50%;
    }
    .dz_main_item:last-child{
        padding-bottom: 0;
    }
    .dz_main_item{
        display: flex;
        flex-direction: column;
        position: relative;
        padding-left:1.2rem;
        padding-bottom: 1.95rem;
        margin-left:.69rem;
        font-size: .8rem;
    }
    .dz_main{
        list-style: none;
        margin:0 1.5rem;
        padding-top:4.1rem;
        border-left:1px solid #eee;
    }
    .dz_dingdan_zuizong{
        z-index:99;
        position: relative;
        width: 100%;
        height:100%;
        overflow-y: scroll;
        overflow-x: hidden;
        background-color: #fff;
    }

    /*  */
    .dz_h_header{
        z-index:99;
        position: fixed!important;
        top:0;
        left:0;
        width: 100%;
    }
</style>



