<template>
    <div class="sf_search_foods">
        <search class="sf_search" @get_food="get_food"></search>
        <paixu class="sf_paixu" :peisongfs="peisongfs" :xiaofei="xiaofei" :huodong="huodong" :shangjia="shangjia" :food="food" @search_food="search_food"></paixu>
        <main class="px_foods">
            <section class="px_foods_item" :key="item.id" v-for="item in food">
                <div class="px_shop_title">
                    <img :src="find_img(item.restaurant.image_path)+'?imageMogr/format/webp/thumbnail/64x/'" alt="" class="px_foods_titleImg"/>
                    <router-link :to="{path:'/shop',query:{id:item.restaurant.id}}" class="px_foods_shopInfo px_shopLink">
                        <div class="px_shopInfo_top">
                            <div class="px_shopInfoTop_name">
                                <span class="pinpai" v-if="item.restaurant.is_premium"></span>
                                <span v-html="highLight_word(item.restaurant.name)"></span>
                            </div>
                            <div class="zhuanSong" v-if="item.restaurant.delivery_mode&&item.restaurant.delivery_mode.text"></div>
                        </div>
                        <div class="px_shopInfo_bottom">
                            <p class="px_bottom_qisong">
                                <span>{{item.restaurant.rating}}</span>
                                <span>{{item.restaurant.float_minimum_order_amount}}</span>
                                <span>{{item.restaurant.float_delivery_fee}}</span>
                            </p>
                            <div class="px_bottom_time">
                                <span class="px_shopinfo_km">{{distance(item.restaurant.distance)}}</span>
                                <span class="px_shopinfo_minute">{{item.restaurant.order_lead_time}}</span>
                            </div>
                        </div>
                    </router-link>
                </div>
                <ul class="px_foods_food">
                    <router-link :to="{path:'/shop',query:{id:item.restaurant.id}}" class="px_foods_foodItem px_shopLink" :key="food.id" v-for="food in item.foods">
                        <img :src="find_img(food.image_path)+'?imageMogr/format/webp/thumbnail/128x/'" alt="" class="px_foods_foodImg" />
                        <div class="px_foodItem_info">
                            <div class="px_foodItem_infoName" v-html="highLight_word(food.name)"></div>
                            <div class="px_foodItem_infoSale">{{food.month_sales}} {{food.satisfy_rate}}%</div>
                            <div class="px_foodItem_infoPrice">{{food.price}}
                                <del class="px_foodItem_oldPrice" v-if="food.original_price&&food.activities[0]">{{food.original_price}}</del>
                                <span v-if="food.activities.length" :class="food.activities[0].icon_name==''?'zekou':'manjian'">{{food.activities[0].tips}}</span>
                            </div>
                        </div>
                    </router-link>
                </ul>
                <div class="px_foods_more" v-if="item.foods.length>2" v-on:click="showlist($event,item.foods.length-2)">{{item.foods.length-2}} <span class="icon icon-down"></span></div>
            </section>
            <section class="px_saixuan_open" v-on:click='hidden_sub'></section>
            <div style="height:2rem;">
                <div class="wm_loading" v-if="loading"><img src="./img/loading.gif" alt="" style="width:.8rem;height:.8rem;vertical-align: top;margin-right:0.3rem;"/>...</div>
                <div class="wm_loading" v-if="notHave">~</div>
            </div>
        </main>
    </div>
</template>

<script>
    import paixu from './paixu.vue'
    import search from './search.vue'
    export default{
        name:'search_foods',
        data(){
            return{
                offset:8,//
                keyword:this.$route.query.keyword,
                food:[],
                showFoods:false,
                loading:false,// 
                notHave:false,// 
                peisongfs:[''],
                xiaofei:[''],
                huodong:[''],
                shangjia:[],
            }
        },
        mounted(){
            $(()=>{
                let box = $('.px_foods');

                box.scroll(()=>{
                    let height = Math.floor(box[0].scrollHeight - box.height());
                    if(box.scrollTop()==height||box.scrollTop()>=height-1){//
                        this.search_food(this.peisongfs,this.xiaofei,this.huodong,this.shangjia);
                    }
                });
            });//

            //
            $('.px_foods').height($(window).height()-$('.sf_paixu').innerHeight()-$('.sc_search').innerHeight())
        },
        methods:{
            get_food(food){
                this.food = food;
            },
            search_food(peisongfs,xiaofei,huodong,shangjia,clear){
                if(clear){
                    this.food = [];
                    this.offset = 0
                }

                this.notHave = false;
                this.loading = true;
                $.get('/search',{
                    keyword:this.keyword,
                    offset:this.offset,
                    peisong:peisongfs,
                    xiaofei:xiaofei,
                    huodong:huodong,
                    shangjia:shangjia,
                    lat:$.cookie('lat'),
                    lit:$.cookie('lit'),
                },data=>{
                    //keyvalue   
                    let chunk = data['0']||data['1']||data['3'];
                    console.log(data);

                    //chunk   
                    if(chunk){
                        let length = chunk.restaurant_with_foods.length;
                        for(let i = 0;i<length;i++){
                            this.food.push(chunk.restaurant_with_foods[i]);
                        }
                        this.loading = false;
                        this.offset += 8;
                    }else{
                        this.loading = false;
                        this.notHave = true;
                    }
                });
            }, //
            hidden_sub(){
                // 
                if(this.show_sub.open){
                    this.show_sub.open.css('visibility','hidden');
                    this.show_sub.parent.css('overflow-y','scroll');

                    this.show_sub.shaixuan_sub.css({
                        opacity:'0',
                        height:'0',
                    });
                    this.show_sub.paixu_sub.css({
                        opacity:'0',
                        height:'0',
                    })
                }
            },//    heightopacity
            distance(km){
                return km>1000?(km/1000).toFixed(2)+'km':km+'m';
            },//MKM 1K 
            find_img(String_path){
                let path = String_path+'';
                let format = path.match(/(jpeg|png|jpg|bmp)/)[0];
                return `https://fuss10.elemecdn.com/${path.slice(0,1)}/${path.slice(1,3)}/${path.slice(3)}.${format}`;
            },//imgimg  967284af9fb1b499192d2476648c3ccfjpeg   https://fuss10.elemecdn.com/9/67/284af9fb1b499192d2476648c3ccfjpeg.jpeg
            highLight_word(text){
                return text.split(this.keyword).join(`<span style="color:#118dff;">${this.keyword}</span>`);
            },//
            showlist(e,num){
                // dom
                let target = $(e.currentTarget);
                //dom   ul
                let list = target.prev();

                //   
                if(list.css('maxHeight')=='none'){
                    list.css('maxHeight','9rem');
                    target.html(''+num+' <span class="icon icon-down"></span>');
                }else{
                    list.css('maxHeight','none');
                    target.html(' <span class="icon icon-up"></span>');
                }
            },//
        },
        components:{
            paixu,
            search
        }
    }

</script>

<style scoped>
    /*  */
    .wm_loading{
        width: 100%;
        padding:.6rem 0;
        text-align: center;
        font-size:.5rem;
        color: #666;
    }

    /*  */
    .zekou{
        background: linear-gradient(-135deg, rgb(255, 130, 45) 0%, rgb(255, 42, 0) 100%); color: rgb(255, 255, 255);
        height:.9rem;
        padding:0 .1rem;
        line-height: .9rem;
        font-size:.5rem;
        transform: scale(.9);
    }
    .manjian{
        height:.9rem;
        padding:0 .1rem;
        border:.01rem solid rgb(240, 115, 115);
        line-height: .9rem;
        font-size:.5rem;
        color:rgb(240, 115, 115);
        transform: scale(.9);
    }
    .pinpai{
        padding:.05rem .1rem;
        background:linear-gradient(-139deg,#fff100,#ffe339);
        font-weight: 700;
        font-size: .5rem;
        text-align: center;
        white-space: nowrap;
        color:#6f3f15;
        border-radius: .12rem;
    }
    .px_shopLink{
        color: #444444;
        text-decoration: none;
    }

    /*  */
    .px_foods{
        overflow-y: scroll;
        position: relative;
        height:100%;
        background: #fff;
        transition: background .3s;
    }
    .px_foods_item{
        font-size:.5rem;
    }
    .px_shop_title{
        display: flex;
        align-items: center;
        height:3.3rem;
        padding:0 .8rem;
        border-bottom:.01rem solid #f6f6f6;
    }
    .px_foods_titleImg{
        width:1.9rem;
        height:1.9rem;
    }
    .px_foods_shopInfo{
        display: flex;
        flex-wrap: wrap;
        flex: 1;
        width:100%;
        margin-left: .3rem;
    }
    .px_shopInfo_top{
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-between;
        width:100%;
    }
    .px_shopInfoTop_name{
        font-size:.7rem;
    }
    .px_shopInfo_bottom{
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-between;
        width:100%;
    }
    .px_bottom_time{
        transform:scale(.9);
    }
    .px_bottom_time span+span:before{
        content: '|';
        margin-right:.16rem;
    }
    .px_bottom_qisong{
        display: flex;
        text-align: left;
        margin-left:-.34rem;
        transform: scale(.9);
    }
    .px_bottom_qisong span+span:before{
        margin:0 .13rem 0 0;
        color: #ddd;
        content: "|";
    }
    .px_foods_food{
        overflow: hidden;
        list-style: none;
        max-height:9rem;
        padding:0 .8rem;
    }
    .px_foods_foodItem{
        display: flex;
        align-items: center;
        height:4.5rem;
        padding:.7rem 0;
        border-bottom:.01rem solid #f6f6f6;
    }
    .px_foods_foodImg{
        width:3rem;
        height:3rem;
        margin-right:.6rem;
    }
    .px_foodItem_info{
        display: flex;
        flex-wrap: wrap;
        align-content: space-between;
        width: 80%;
        height:3.4rem;
    }
    .px_foodItem_infoName{
        width: 100%;
        font-size:.7rem;
    }
    .px_foodItem_infoSale{
        width: 100%;
        color:#666;
    }
    .px_foodItem_infoPrice{
        display: flex;
        align-items: center;
        width: 100%;
        font-size: .75rem;
        color:#ff5339;
    }
    .px_foodItem_oldPrice{
        display:block;
        margin-top:.1rem;
        color:#999;
        transform:scale(.8);
    }
    .px_foods_more{
        padding:.6rem 0;
        text-align: center;
    }

    /*  */
    .sf_search_foods{
        z-index:99;
        position: relative;
        overflow: hidden;
        width:100%;
        height:100%;
        background: #fff;
    }

    .sf_search{
        background: #fff;
    }
</style>


